
import Flexbox from 'vux/src/components/flexbox/flexbox.vue'
import Loading from 'vux/src/components/loading/index.vue'

import noprint from '../../assets/images/noPrint_bg.gif';
import prints from '../../assets/images/main_bg14.png';
export default {
props: {
printFlag: String
},
name: 'mapSelects',
components: {
Flexbox,
Loading
},
data () {
return {
showDialog: false,
selectBuild: 'Tutorial Room - T3.03',
prints: prints,
noprint: noprint
};
},
mounted () {
let _this = this;
this.$Bus.$on('clearSeleBuild', data => {
_this.selectBuild = data;
});
},
methods: {
getBuilds1 () {
this.$Bus.$emit('showDialogs', true);
this.$Bus.$emit('curSelectBuild', {
name: '',
show: false
});
},
getBuilds (name) {
this.selectBuild = name;
this.$Bus.$emit('curSelectBuild', {
name: name,
show: true
});
}
}
};



